<template>
  <div style="border:1px solid blue">
    <el-form ref="form" label-width="80px">
      <el-form-item>
        <el-button type="primary" @click="getEspInfo">获取单片机最新数据</el-button>
      </el-form-item>
      <el-form-item label="作者:">
        {{ info.name || '未知' }}
      </el-form-item>
      <el-form-item label="email:">
        {{ info.email || '未知' }}
      </el-form-item>
      <el-form-item label="输入值:">
        {{ info.PIN_A0 || '未知' }}
      </el-form-item>
      <el-form-item label="输出值:">
        {{ info.PIN_4==='0'?'开':'关' }}
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="switchLed4">Led开关</el-button>
      </el-form-item>
      <el-form-item label="亮度:">
        <el-slider v-model="lm" style="width:300px" />
      </el-form-item>
    </el-form>

  </div>

</template>
<script>
import axios from 'axios'
import { Message } from 'element-ui'

export default {
  name: 'Index',
  components: {

  },
  props: {},
  data() {
    return {
      lm: 0,
      info: { 'name': 'mitking', 'url': '192.168.128.1', 'email': '3293456147@qq.com', 'PIN_A0': '79', 'PIN_4': '0' }
    }
  },
  watch: {
    lm(n) {
      console.log(n)
      axios.get('/switchled4', { params: { lm: n }}).then((response) => {
        console.log(response)
        this.info.PIN_4 = response.data.toString()
      })
    }
  },
  created() {

  },
  mounted() {
    this.getEspInfo()
  },
  methods: {
    getEspInfo() {
      const that = this
      axios.get('/info').then(function(response) {
        console.log(response)
        that.info = response.data
        Message.success('获取ESP8266信息成功')
      })
        .catch(function() {
          that.info = {}
          Message.warning('获取ESP8266信息失败')
        })
    },
    switchLed4() {
      const that = this
      axios.get('/switchled4').then(function(response) {
        console.log(response)
        that.info.PIN_4 = response.data.toString()
        Message.success('操作成功')
      })
    }
  }
}
</script>
