<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import 'element-ui/lib/theme-chalk/display.css'
import Common from '@/components/Common'
import Vue from 'vue'
Vue.prototype.Common = Common
var w_win = document.documentElement.clientWidth || document.body.clientWidth

var _html = document.getElementsByTagName('html')[0]

if (w_win <= 750) {
  Vue.prototype.Common.PHONE = true
  Vue.prototype.Common.W_WIN = w_win
  _html.style.fontSize = w_win / 24 + 'px'
  // _html.style.fontSize = '50px';
} else {
  _html.style.fontSize = '20px'
}

export default {

  name: 'App',
  components: {

  },
  data() {
    return {
      HEIGHT: Number
    }
  },
  mounted() {
    // 获取屏幕宽高度,动态改变文字大小
    var that = this
    window.onresize = () => {
      return (() => {
        // window.clientHeight = document.body.clientHeight;
        w_win = document.body.clientWidth
        that.$store.dispatch('setvalue_win', w_win)
        // 设置根元素字体大小
        if (w_win <= 750) {
          _html.style.fontSize = w_win / 18 + 'px'
        } else {
          _html.style.fontSize = '16px'
        }
      })()
    }
  },
  methods: {
  }
}
</script>

<style>
@import "./App.css"
</style>
