<template>
  <div>
    <el-form v-show="op_show" ref="form" size="mini" class="opreat-form" inline>
      <el-form-item>
        <el-button @click="edit=!edit">{{ edit?'编辑模式':'看图模式' }}</el-button>
      </el-form-item>
      <el-form-item v-if="edit">
        <el-button @click="removeItem">清空数据</el-button>
      </el-form-item>
      <el-form-item>
        <el-input v-model="keyword" style="width:100px" placeholder="关键字" />
      </el-form-item>
      <el-form-item>
        <el-select v-model="store" style="width:80px">
          <el-option :value="-1" label="全部" />
          <el-option :value="0" label="门店A" />
          <el-option :value="1" label="门店B" />
        </el-select>
        <el-form-item>
          <el-button size="mini" plain :icon="op_show?'el-icon-arrow-left':'el-icon-arrow-right'" @click="op_show=!op_show" />
        </el-form-item>
      </el-form-item>
    </el-form>
    <el-button v-show="!op_show" plain class="opreat-form" size="mini" :icon="op_show?'el-icon-arrow-left':'el-icon-arrow-right'" @click="op_show=!op_show" />
    <baidu-map
      :center="map_center"
      :zoom="map_zoom"
      :double-click-zoom="true"
      :scroll-wheel-zoom="true"
      :enable-map-click="true"
      class="baidu-map-view"
      ak="TeRCzSUKWRydZTgqzofw0HcM"
      @ready="map_handler"
    >
      <bm-local-search :keyword="keyword" :auto-viewport="true" location="东港" />
      <div v-for="item in point_arr" :key="item.id">
        <bm-marker
          v-if="item.show"
          shape="BMAP_POINT_SHAPE_STAR"
          :icon="myIconArr[item.store]"
          :position="{lng: item.msg.lng, lat: item.msg.lat}"
          :dragging="edit"
          @click="item.infoshow=!item.infoshow"
        >
          <bm-info-window :show="item.infoshow" :position="{lng: item.msg.lng, lat: item.msg.lat}" :dragging="edit" @close="item.infoshow=false" @open="item.infoshow=true">
            <div class="context">
              <div>地址：{{ item.address }}</div>
              <div>备注：{{ item.remark }}</div>
              <div>手机号：{{ item.tel }}
                <el-button v-show="!edit" size="mini" plain icon="el-icon-phone" circle @click="call(item.tel)" />
              </div>
            </div>
            <el-button v-show="edit" size="mini" @click="edititemfn(item)">编辑</el-button>
            <el-button v-show="edit" size="mini" type="danger" @click="del(item)">删除</el-button>
          </bm-info-window>
          <bm-label :content="item.address" :position="{lng: item.msg.lng, lat: item.msg.lat}" :label-style="{color: '#000', fontSize : '12px'}" :offset="{width:20}" />
        </bm-marker>
      </div>
      <el-button plain circle style="bottom:10px;right:10px" class="opreat-form" icon="el-icon-aim" @click="location" />
    </baidu-map>
    <el-dialog
      title="地点"
      :visible.sync="addformshow"
      width="80%"
      style="z-index:101"
      fullscreen
    >
      <el-form v-show="op_show" ref="addform" label-width="70px">
        <el-form-item label="发货门店">
          <el-select v-model="addform.store">
            <el-option :value="0" label="门店A" />
            <el-option :value="1" label="门店B" />
          </el-select>
        </el-form-item>

        <el-form-item label="地址">
          <el-select v-model="addform.address">
            <el-option v-for="adr in adrarr" :key="adr.uid" :value="adr.title" />
          </el-select>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model.number="addform.tel" />
        </el-form-item>
        <el-form-item label="备注">
          <el-input v-model="addform.remark" />
        </el-form-item>
        <!-- <el-form-item label="信息">
          <div>{{ addform.msg }}</div>
        </el-form-item> -->
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addformshow = false">取 消</el-button>
        <el-button
          type="primary"
          @click="addoredit();addformshow = false"
        >确 认
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { BaiduMap } from 'vue-baidu-map'

export default {
  name: 'Map',
  components: { BaiduMap },
  props: [],
  data() {
    return {
      keyword: '',
      map_center: { lng: 124.159924, lat: 39.888021 },
      point_arr: [],
      store: -1,
      formtype: 1,
      map_zoom: 15,
      edit: false,
      op_show: true,
      addformshow: false,
      infoshow: false,
      myGeo: null,
      bmap: null,
      map: null,
      adrarr: [],
      myIconArr: [
        {
          url: 'http://jiangkou.qiaodu.net/images/markers3.png',
          size: { width: 23, height: 25 },
          opts: {
            anchor: { width: 23, height: 25 },
            imageOffset: { width: 0, height: 0 },
            infoWindowAnchor: { width: 23, height: 25 }
          }
        }, {
          url: 'http://jiangkou.qiaodu.net/images/markers3.png',
          size: { width: 23, height: 25 },
          opts: {
            imageOffset: { width: 0, height: -25 }
          }
        }
      ],
      addform: {
        tel: '',
        store: 0,
        address: '',
        remark: '',
        msg: {}
      }
    }
  },
  watch: {
    store(n) {
      this.point_arr.map(item => {
        if (n === -1) {
          item.show = true
        } else {
          item.show = item.store === n
        }
      })
    },
    keyword(n) {
      if (!n) {
        this.$nextTick(() => {
          this.keyword = ' '
        })
      }
    }

  },
  mounted() {
    if (localStorage.getItem('point_arr')) {
      this.point_arr = JSON.parse(localStorage.getItem('point_arr'))
    }
  },
  methods: {
    removeItem() {
      localStorage.removeItem('point_arr')
      this.point_arr = []
    },
    addoredit() {
      if (this.formtype === 1) {
        this.point_arr.push({ id: 'id_' + this.point_arr.length, show: true, msg: this.addform.msg, remark: this.addform.remark, address: this.addform.address, infoshow: false, tel: this.addform.tel, store: this.addform.store })
        localStorage.setItem('point_arr', JSON.stringify(this.point_arr))
      } else {
        this.point_arr.map(item => {
          if (item.id === this.addform.id) {
            item = this.addform
          }
        })
      }
    },
    edititemfn(item) {
      this.formtype = 2
      this.addform = item
      this.addformshow = true
    },
    call(tel) {
      window.location.href = `tel:${tel}`
    },
    map_handler({ BMap, map }) {
      this.bmap = BMap
      this.map = map
      map.addEventListener('click', this.mapclickfn)
    },
    location() {
      this.map.reset()
    },
    mapclickfn(e) {
      document.activeElement.blur()
      var myGeo = new this.bmap.Geocoder()
      var pt = new this.bmap.Point(e.point.lng, e.point.lat)
      var _this = this
      myGeo.getLocation(pt, function(result) {
        _this.adrarr = result.surroundingPois
      })
      if (this.edit && !e.overlay) { // 编辑点击地图时 = 新增地点
        this.addform = this.$options.data().addform
        this.addform.address = _this.adrarr.length && _this.adrarr[0].title || ''
        this.formtype = 1
        this.addform.msg = e.point
        this.addformshow = true
      }
    },
    del(item) {
      this.point_arr = this.point_arr.filter(it => it.id !== item.id)
      localStorage.setItem('point_arr', JSON.stringify(this.point_arr))
    }
  }
}
</script>
<style scoped>
.baidu-map-view{
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
}
.opreat-form{
  /* border:1px solid blue; */
  position: absolute;
  z-index: 100;
}
.context{
  font-size:14px;
  padding: 10px
}
</style>
